/* Enhanced styling for the app */

.App {
  text-align: center;
  font-family: 'Arial', sans-serif;
  background-color: #f4f4f4; /* Light grey background for the whole app */
  color: #333; /* Dark grey text for better readability */
}

.cool-title {
  text-align: center;
  font-size: 2.5em; /* Large font size */
  font-family: "AmericanTypewriter", "American Typewriter", Raleway Dots, cursive;
  color: hsl(0, 0%, 10%); /* Greenish color, you can choose any color you like */
  margin: 5px 0;
  padding: 10px;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.1); /* Light background for contrast */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Subtle shadow for depth */
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.2); /* Slight text shadow for readability */
  transform: skew(-10deg); /* Slight skew for a dynamic look */
}

@media (max-width: 768px) {
  .cool-title {
    font-size: 1.5em; /* Smaller font size on smaller screens */
    transform: skew(0deg); /* Remove skew on smaller screens for better readability */
  }
}


.header {
  min-height: 15vh; /* Slightly larger header */
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(12px + 2vmin); /* Slightly larger text */
  color: #fff; /* White text for contrast */
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.3); /* Add shadow to header */
}

.link-wrapper {
  text-decoration: none; /* Removes underline from links */
  color: inherit; /* Keeps the text color the same as the div */
}

.parking-lot-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px; /* Add gap between cards */
}

.parking-lot-card {
  display: flex; /* Enables Flexbox */
  flex-direction: column; /* Stacks children vertically */
  align-items: center; /* Horizontally centers the content */
  justify-content: center; /* Vertically centers the content */
  text-align: center; /* Center text alignment */
  border: none; /* Remove border */
  border-radius: 10px; /* Rounded corners */
  width: 280px; /* Adjust width */
  background-color: #fff; /* White background for cards */
  box-shadow: 0 6px 12px 0 rgba(0, 0, 0, 0.15); /* Softer shadow */
  transition: transform 0.3s, box-shadow 0.3s; /* Animate shadow and transform */
  padding: 10px; /* Add padding */
}

.parking-lot-card h2 {
  color: #1a1a1a; /* Use the header color for titles */
  font-size: 16px; /* Reduced font size for title */
  margin-bottom: 0px; /* Reduced space below title */
  /* Ensure H2 takes full width for proper centering */
  width: 100%;
}

.parking-lot-card p {
  text-align: left; /* Align the <p> to the left */
  width: 100%; /* Ensure P takes full width */
  margin: 0; /* Remove default margin */
  align-self: flex-start; /* Align to the start of the flex container */
}


.parking-lot-card.free {
  background-color: #6BD183; /* Green for free */
}

.parking-lot-card.almostFull {
  background-color: #A8CCB0; /* Orange for almost full */
}

.parking-lot-card.full {
  background-color: #CECECE; /* Red for full */
}

.parking-lot-card:hover {
  transform: translateY(-5px); /* Move card up slightly on hover */
  box-shadow: 0 12px 24px 0 rgba(0, 0, 0, 0.2); /* Larger shadow on hover */
}



.parking-lot-card p {
  color: #333; /* Darker text for readability */
  font-size: 14px; /* Reduced font size for paragraph */
  line-height: 1; /* Adjusted line spacing */
}

.modern-select {
  padding: 10px 15px;
  border: 2px solid #ddd; /* Light grey border */
  border-radius: 5px; /* Rounded corners */
  font-size: 16px; /* Larger font size */
  background-color: white;
  appearance: none; /* Removes default styling */
  outline: none; /* Removes the outline */
  cursor: pointer; /* Changes cursor to pointer */
  margin: 10px 0; /* Adds some margin */
}

.select-wrapper {
  position: relative;
  display: inline-block;
}

.modern-select {
  padding: 10px 30px 10px 10px; /* Add more padding on the right for the arrow */
  border: 2px solid #ddd;
  border-radius: 5px;
  font-size: 16px;
  background-color: white;
  appearance: none; /* Removes default styling */
  outline: none;
  cursor: pointer;
  margin: 10px 0;
}

.modern-select:hover, .modern-select:focus {
  border-color: #aaa; /* Slightly darker border on hover/focus */
}

/* Style for the custom arrow */
.select-arrow {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  pointer-events: none; /* Makes sure clicks on the arrow still open the dropdown */
  color: #333;
  font-size: 20px;
}

/* Hide the native arrow in Firefox */
.modern-select::-moz-focus-outer {
  border: 0;
}


.loading-indicator {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  height: 100vh; /* This will center the loader vertically on the page */
}

.spinner {
  border: 4px solid rgba(0, 0, 0, 0.1); /* Light grey border */
  border-top: 4px solid #333; /* Darker color for the spinning part */
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}


.error-message-button {
  background-color: #dc3545; /* Red background */
  color: white; /* White text */
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  margin: 20px 0;
  transition: background-color 0.3s;
}

.error-message-button:hover {
  background-color: #bd2130; /* Darker red on hover */
}


